
.myProjectsBlock {
    min-height: 100%;
    background-color: #101010;
    padding-bottom: 30px;
}

.myProjectsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.myProjects {
    border: 1px solid;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
}

@media screen and (min-width: 1315px) {
    .myProjects {
        justify-content: space-between;
    }
}


